.scrollbars {
  position: relative;
  overflow: hidden !important;
}

.scrollbars__scroll-wrapper {
  height: 100%;
  margin-right: -20px;
  padding-right: 10px;
  margin-bottom: -20px;
  padding-bottom: 10px;
  overflow-Y: scroll;
}

.scrollbars__bar-wrapper-x,
.scrollbars__bar-wrapper-y {
  position: absolute;
  border-radius: 6px;
  background: rgba(0,0,0, .1);
  opacity: 0;
  transition: opacity 300ms ease 200ms;
}

.scrollbars__bar-wrapper-y {
  top: 4px;
  bottom: 4px;
  right: 0;
  width: 6px;

  .scrollbars__bar {
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.scrollbars__bar-wrapper-x {
  bottom: 0;
  left: 4px;
  right: 4px;
  height: 6px;

  .scrollbars__bar {
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

.scrollbars__bar {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0, .25);
  border-radius: 6px;
}

.scrollbars:hover .scrollbars__bar-wrapper-y,
.scrollbars:hover .scrollbars__bar-wrapper-x,
.scrollbars__bar-wrapper--grabbed {
  opacity: 1;
}
